<template>
    <div class="mBg">
        <mhead-top titleHead="logo" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-shopping-cart-1 icon" rgUrl="/cart/cart"></mhead-top>
        <div class="">
            <div class="m-Swiper" v-if="info.list_pic_url && info.list_pic_url.length!=0">
                <el-carousel trigger="click">
                    <el-carousel-item v-for="(item,index) in info.list_pic_url" :key='index'>
                        <el-image class="m-Simg" :src="item" fit="contain"></el-image>
                    </el-carousel-item>
                </el-carousel>
            </div>
            <div class="m-PInfo">
                <h1 class="cname">{{info.good_name}}</h1>
                <p class="ename">{{info.good_english_name}}</p>
                <p class="cas" v-if="info.cas!=''">CAS号：{{info.cas}}</p>
                <div class="mPrice">
                    <div class="mPrice-it" v-for="(item,index) in info.spe_data" :key="index">
                        <div class="mPrice-top">
                            <span class="mPrice-type">{{info.price_type}}</span>
                            <span>{{item.unit_price}}<span class="mP-unit">/{{info.supply_unit}}</span></span>
                        </div>
                        <div class="mPrice-foot">{{item.price_name}}<span>{{info.supply_unit}}</span></div>
                    </div>
                </div>
            </div>
            <div class="m-PSend">
                <div class="m-d-line bbGray" @click="putOnDrawer()">
                    <div class="m-d-lf">已选</div>
                    <div class="m-d-rg wto">{{info.spec_name}}{{info.spec_unit}}，购买{{form.num}}{{info.supply_unit}}</div>
                    <i class="el-icon-arrow-right icon"></i>
                </div>
                <el-drawer
                        class="g-drawer"
                        title="选择规格"
                        :visible.sync="choiceDrawer"
                        direction="btt">
                    <div class="choiceBox">
                        <div class="c-coi-top flex f-nowrap">
                            <div class="c-coi-pic" v-if="info.list_pic_url && info.list_pic_url.length!=0">
                                <el-image class="c-eImg" :src="info.list_pic_url[0]" fit="cover" :preview-src-list="info.list_pic_url"></el-image>
                            </div>
                            <div class="c-coi-rg">
                                <div class="c-coi-price flex f-nowrap">
                                    <div :class="specSelIndex==index?'c-oi-item active':'c-oi-item'" v-for="(item,index) in info.spe_data" :key="index">
                                        <span class="c-oi-p">{{info.price_type}}{{item.unit_price}}<label>/{{info.supply_unit}}</label></span>
                                        <span>{{item.price_name}}{{info.supply_unit}}</span>
                                    </div>
                                </div>
                                <div class="c-coi-stroage">库存：{{info.storage}}{{info.supply_unit}}</div>
                            </div>
                        </div>
                        <div class="c-coi-mid">
                            <h1>规格</h1>
                            <div class="c-coi-size">
                                <el-row>
                                    <el-button size="mini"  type="primary">{{info.spec_name}}{{info.spec_unit}}</el-button>
                                </el-row>
                            </div>
                        </div>
                        <div class="flex f-nowrap j-bet mt5">
                            <div>
                                <h1>数量</h1>
                                <div class="c-coi-num">
                                    <el-input-number size="mini" :min="info.buyNum[0]" :max="parseInt(info.storage)" @change="numberChange" v-model="form.num"></el-input-number>
                                </div>
                            </div>
                            <div class="c-coi-cout">
                                <span class="color9 mr5">共</span>{{form.num}}<span class="color9 ml5 mr10">{{info.supply_unit}}</span>{{info.price_type}}{{total}}
                            </div>
                        </div>
                        <div class="c-coi-foot">
                            <div class="c-coi-btn">
                                <el-button class="f16 mD-btn" plain @click="addCart">加入购物车</el-button>
                                <el-button class="f16 mD-btn" type="primary" @click="buyNow">立即购买</el-button>
                            </div>
                        </div>
                    </div>
                </el-drawer>
                <div class="m-d-line" @click="selAddress()">
                    <div class="m-d-lf">配送</div>
                    <div class="m-d-rg wto">{{addressId?choosedAddress:'请选择配送地址'}}</div>
                    <i class="el-icon-arrow-right icon"></i>
                </div>
            </div>
            <div class="m-evaluate"  v-if="evaluate[0]">
                <div class="m-eva-top bbGray" @click="toEvaluate(info.spec_id)">
                    <div class="m-eva-lf">用户评价（{{evaluate.length}}）</div>
                    <div class="m-eva-rg">查看更多</div>
                    <i class="el-icon-arrow-right icon"></i>
                </div>
                <div class="m-eva-content">
                    <div class="m-eva-tit">
                        <span>{{evaluate[0].nickname}}</span>
                        <el-rate
                                class="m-eva-star"
                                :value="evaluate[0].goods_rank"
                                disabled>
                        </el-rate>
                    </div>
                    <div class="m-eva-score">
                        <span class="color9">{{evaluate[0].create_time}}</span>
                    </div>
<!--                        <div class="m-eva-time">-->
<!--                            <span>{{evaluate[0].time}}</span>-->
<!--                            <span class="ml15">{{evaluate[0].spec}} 纯度：{{evaluate[0].purity}}</span>-->
<!--                        </div>-->
                    <div class="m-eva-content">
                        {{evaluate[0].content}}
                    </div>
                </div>
                <div class="m-eva-pic">
                    <div class="mEvaImg" v-for="(item,index) in evaluate[0].photos" :key="index">
                        <el-image class="meImg" :src="item" fit="fill" :preview-src-list="evaluate[0].photos"></el-image>
                    </div>
                </div>
            </div>
            <div class="m-pro-detail">
                <h1 class="bbGray">商品详情</h1>
                <div class="m-pro-content" v-html="info.description"></div>
            </div>
        </div>
        <div class="m-pro-foot">
            <div class="mpf">
                <div class="onCollect" @click="toCollect"><i :class="info.isCollect?'el-icon-star-on icon':'el-icon-star-off icon'"></i></div>
                <div class="addCart" @click="putOnDrawer()">加入购物车</div>
                <div class="buyNow" @click="putOnDrawer()">立即购买</div>
            </div>
        </div>
    </div>
</template>


<script>
    import {goodDetail,getComment,addToCart,addCollection,delMyCollection,address} from '../../service/api'
    import {mapState, mapMutations, mapActions} from 'vuex'
    import mheadTop from '../../components/mobile/headTop';

    export default {
        name: 'mGoodDetail',
        data(){
            return{
                goodId:'',
                specifications_id:'',
                info: {
                    spe_data: [],
                    buyNum:[]
                },
                choiceDrawer:false,//选择规格拉起
                specSelIndex:0,
                form:{
                    goodId:'',
                    specId:'',
                    num:1
                },
                evaluate:[],
                total:''
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','choosedAddress','addressId','cartList','cartLength'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo','initCart'
            ]),
            ...mapMutations([
                'CHOOSE_ADDRESS'
            ]),
            async initData(){
                // 获取产品详细信息
                this.specifications_id = this.$route.query.specifications_id;
                let goodRes = await goodDetail(this.specifications_id);
                this.goodId = goodRes.goods_id;
                this.info = goodRes;
                this.form.num = this.info.buyNum[0];//初始购买数量要以buyNum的最小购买量来算，不能简单设为1
                this.calculate();
                // 获取产品评价
                let evaluateRes = await getComment(
                    this.info.spec_id,
                    '',
                    ''
                );
                this.evaluate = evaluateRes;
                //获取默认地址
                this.getAddress();
            },
            buyNow(){
                if (this.userInfo && this.userInfo.user_id){
                    //0:购物车下单，1：产品详细页下单
                    this.$router.push({path:"/order/confirm",query:{goodsId:this.goodId,speId:this.info.spec_id,number:this.form.num,type:1}})
                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            async addCart(){
                //goodsId,speId,number
                let res = await addToCart(this.goodId, this.info.spec_id, this.form.num);
                if(res){
                    this.$message({
                        message: res.msg,
                        type: res.status==12000?'success':'warning'
                    });
                    this.initCart();
                }
            },
            numberChange(value){
                var unitPrice = this.info.spe_data[0].unit_price;
                var belongLevel = 0;
                for(var i = 0 ; i < this.info.buyNum.length ; i++){
                    if(value>=this.info.buyNum[i]){
                        belongLevel++;
                    }
                }
                this.specSelIndex = belongLevel-1;
                this.calculate();
            },
            calculate(){
                var unitPrice = this.info.spe_data[this.specSelIndex].unit_price;
                var num  = this.form.num;
                this.total = ((parseInt(num)*parseInt(parseFloat(unitPrice)*100))/100).toFixed(2);
            },
            selAddress(){
                if (this.userInfo && this.userInfo.user_id){
                    this.$router.push({path:"/order/address",query:{speId:this.specifications_id}});
                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            async toCollect(){
                if(this.info.isCollect){
                    //已经收藏过
                    this.$confirm('你确定要取消该收藏吗?', '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(async ()=> {
                        let res = await delMyCollection(this.info.collectId);
                        if(res.status == 12000){
                            this.$message({
                                message:res.msg,
                                type:'success'
                            });
                            this.info.isCollect = false;
                        }else{
                            this.$message({
                                message:res.msg,
                                type:'warning'
                            });
                        }
                    }).catch(() => {

                    });

                }else{
                    //goods_id,specifications_id,rows,page
                    var resAdd = await addCollection(
                        this.info.goods_id,
                        this.info.spec_id
                    );
                    if(resAdd){
                        this.$message({
                            message: resAdd.msg,
                            type: resAdd.status==12000?'success':'warning'
                        });
                        this.initData();
                    }
                }


            },
            toEvaluate(spec_id){
                this.$router.push({path:"/good/evaluate",query:{spec_id:spec_id}})
            },
            putOnDrawer(){
                this.choiceDrawer =true;
            },
            async getAddress(){
                if(!this.addressId){
                    var addList = await address();
                    if(addList.status!=12007){
                        for(var i = 0 ; i < addList.length ; i++){
                            if(addList[i].is_default==true){
                                this.CHOOSE_ADDRESS(addList[i],addList[i].add_id);
                            }
                        }
                    }

                }

            }
        },
        created(){
            //this.form.specId= this.info.spe_data[0].specifications_id;
        }

    }

</script>

